#sidebar-final {
  background: #fff;
  /* width: 156px; */
  width: 200px;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 103px;
  left: 0;
  flex-direction: column;
  padding: 1rem 2rem;
  padding-bottom: 1.5rem;
  z-index: 1000;
  /* padding-top: 3rem; */
  box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
}
.sidebar-final-brand-logo {
  cursor: pointer;
  width: 70%;
  padding-top: 1.5rem;
}
.sidebar-final-icon-name-cont {
  display: flex;
  margin-left: 1rem;
  cursor: pointer;
}
.sidebar-final-icon {
  width: 11%;
  height: 11%;
  margin: auto;
  margin-right: 0.5rem;
}
.sidebar-final-funding-icon {
  width: 7%;
  height: 7%;
  margin: auto;
}
.sidebar-final-mentor-icon {
  width: 13%;
  height: 13%;
  margin: auto;
}
.sidebar-final-icon-name {
  width: 80%;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 36px;
  color: #202020;
}

.sidebar-final-line {
  border: 1px solid #2a72de;
  width: 133px;
  height: 0px;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}
.sidebar-final-icon-logout {
  width: 20%;
  margin-right: 0.5rem;
  z-index: 1;
}
.sidebar-final-logout-btn {
  margin-top: 2rem;
  padding: 8px 32px;
  width: 150px;
  height: 40px;
  background: linear-gradient(
    99.02deg,
    #2a72de 19.49%,
    rgba(42, 114, 222, 0.823601) 49.43%,
    rgba(42, 114, 222, 0.66705) 74.79%,
    rgba(42, 114, 222, 0.324771) 102.8%,
    rgba(42, 114, 222, 0) 131.08%
  );
  box-shadow: 0px 3.39883px 12.7456px -0.849707px rgba(0, 0, 0, 0.25);
  border-radius: 32px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;

  color: #ffffff;
  border: none;
}

.sidebar_link.active {
  /* background: var(--sidebar-hover);
   */
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  border-radius: 10.1694px;
  text-decoration: none;
  background: #ebf0fe;
  /* color: #000; */
}

.sidebar_link {
  text-decoration: none;
  padding: 1rem 0;
}

@media (max-width: 701px) {
  #sidebar-final {
    padding: 1.1rem;
  }
}

@media (max-width: 600px) {
  #sidebar-final {
    top: 0px;
    z-index: 99;
    padding-top: 100px;
    height: 100%;
  }
}
