.main_table {
  width: 100%;
  min-width: 200px;
  border: 2px solid #e2e2e2;
  text-align: left;
  vertical-align: middle;
  border-collapse: separate;
  border-spacing: 0;
  table-layout: fixed;
  border-radius: 24px;
}

.main_table th:first-child {
  border-top-left-radius: 24px;
}

.main_table th:last-child {
  border-top-right-radius: 24px;
}

.main_table th {
  background-color: #ececec;
  padding: 16px 30px;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.045em;
  vertical-align: middle;
  color: #8c8c8c;
  cursor: pointer;
}

.main_table th span {
  margin: 0 10px;
}

.main_table td {
  padding: 10px 30px;
  font-weight: 600;
  border-bottom: 0.953623px solid #eeeeee;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.045em;
  color: #454545;
}

.main_table > tbody > tr {
  border: 0.953623px solid #eeeeee;
  padding: 10px;
  margin-left: 90px;
}

.main_table td span {
  margin: 0 10px;
}

.download_button {
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  letter-spacing: 0.045em;
  outline: none;
  border: none;
  background: transparent;
  cursor: pointer;
  color: #1963ea !important;
}

@media (max-width: 1225px) {
  .download_button,
  .main_table td {
    font-size: 10px !important;
  }
  .main_table th {
    padding: 7px;
  }
}

@media (max-width: 1135px) {
  .main_table th span,
  .main_table td span,
  .main_table td {
    padding: 6px 9px;
  }

  .main_table th span,
  .main_table td span {
    margin: 0 3px;
  }
}

@media (max-width: 550px) {
  .download_button,
  .main_table td {
    font-size: 8px !important;
  }
  .main_table th {
    font-size: 9px !important;
  }
  .main_table th {
    margin: 6px;
  }
  .main_table th span,
  .main_table td span {
    margin: 0px -6px;
  }
}
