#navbar-final {
  width: 100%;
  height: 102px;
  background-color: var(--navbar-clr);
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 10000;
  border-bottom: 0.3px solid #b4b4b4;
}
#navbar-finalScrolled {
  width: 100%;
  height: 102px;
  background-color: var(--navbar-scroll-bg);
  backdrop-filter: blur(7px);
  box-shadow: rgba(0, 0, 0, 0.075);
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 10000;
}
.navbar-brand-logo-img-cont {
  align-items: center;
  justify-content: center;
  display: flex;
  margin-left: 2rem;
}
.navbar-final-brand-logo-img {
  cursor: pointer;
  /* width: 100%; */
  height: 5rem;
}
.navbar-icons-cont {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  margin-right: 5rem;
}
.nabar-final-setting-cont,
.nabar-final-msg-cont,
.nabar-final-userProfile-cont,
.nabar-final-requestIcon-cont {
  cursor: pointer;
}
.nabar-final-requestIcon-cont {
  width: 25px;
}

.navbar-topp-social-icon {
  background: var(--bg-sub-card);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 43.65px;
  height: 43.65px;
  border-radius: 11px;
  display: flex;
  align-items: center;

  cursor: pointer;
}

.navbar-topp-social-icon:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
}
.NavbarPostUploaddSquareCont {
  background-color: var(--btnNewColorTheme);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 7px;
  width: 28px;
  height: 28px;
  cursor: pointer;
}
.NavbarPostUploaddSquareContAddImg {
  font-size: 1.4rem;
  color: var(--comment-card-div);
}

.nabar-final-setting-Icon {
  color: var(--btnNewColorTheme);
  font-size: 1.7rem;
}
.nabar-final-userProfile-Icon {
  color: var(--btnNewColorTheme);
  font-size: 1.4rem;
}
.setting-social-icon-cont {
  position: relative;
}

.setting-dropdown-cont {
  width: 200px;
  /* height: 250px; */
  height: 150px;
  /* background-color: var(--bg-sub-card);
   */
  background-color: #ffffff;
  border-radius: 6px;
  position: absolute;
  /* bottom: -18.5rem; */
  right: -5rem;
  bottom: -12.5rem;
  display: flex;
  flex-direction: column;
  cursor: default;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 1rem;
}
.notifiction-dropdown-cont {
  width: 325px;
  height: fit-content;
  /* background-color: var(--bg-sub-card); */
  background-color: #ffffff;
  border-radius: 6px;
  position: absolute;
  bottom: -4rem;
  right: 4rem;
  display: flex;
  flex-direction: column;
  cursor: default;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 1rem;
}

.notifiction-dropdown-Request-Cont {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: var(--main-text);
  /* background: var(--bg-card); */
  color: black;
  padding: 0.5rem;
  border-radius: 4px;
  display: flex;
  align-items: center;
}

.notifiction-dropdown-Request-name {
  font-size: 17px;
  font-weight: bold;
  margin-right: 0.5rem;
}
.notifiction-dropdown-Request-image {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-top: 0.3rem;
  margin-right: 0.5rem;
}
.notifiction-dropdown-Request-accept,
.notifiction-dropdown-Request-reject {
  cursor: pointer;
  margin-left: 0.5rem;
}

.setting-dropdown-button {
  width: 77%;
  margin: auto;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  background: #2a72de;
  border-radius: 6px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  color: white;
  border: none;
  cursor: pointer;
}

.navbar-changeThemeIcon {
  font-size: 1.3rem;
  color: var(--change-theme);
}

.navbar-themeToggler {
  display: flex;
  margin-right: 3rem;
  margin-top: 0.5rem;
}

.navbar-themeToggler .css-l8tmy5 input::after {
  content: "";
  position: absolute;
  top: -2px;
  right: 2px;
  display: block;
  -webkit-transition: all 250ms ease-in-out;
  transition: all 250ms ease-in-out;
  transition-property: top, right, width, height, border-radius,
    background-color;
  width: 18px;
  height: 32px;
  /* border-radius: 16px; */
  background-color: #e2e8f0;
}
.userIconNavbarFinl {
  width: 50%;
  height: 50%;
}

.navbar_final_upgrade_btn {
  width: 95px;
  height: 32px;

  background: rgba(42, 114, 222, 0.92);
  box-shadow: 0px 4.5614px 17.1053px rgba(0, 0, 0, 0.1);

  font-family: "Exo";
  font-style: normal;
  font-weight: 700;
  font-size: 0.8rem;
  line-height: 16px;
  color: #ffffff;
  border-radius: 20px;
  border: none;
  margin: auto;
  cursor: pointer;
  /* margin-right: 3rem; */
}

.navbar_final_user_Image {
  width: 47px;
  height: 47px;
  border-radius: 50%;
  cursor: pointer;
  /* margin-right: 3rem; */
}

.navbar_noOuterContCSS {
  background: transparent !important;
  padding: 0 !important;
  width: fit-content !important;
  height: fit-content !important;
  margin: 0 3rem;
  box-shadow: none !important;
  border: none !important;
}
.navbar_noOuterContCSS:hover {
  box-shadow: none !important;
}
.nabar-final-notificationBell {
  font-size: 1.7rem;
  /* margin-right: 2rem; */
  color: black;
}
.nabar-final-notificationBell1 {
  font-size: 1.7rem;
  margin-left: -0.2rem;
  color: red;
}

.nabar-final-calenderBell {
  font-size: 1.2rem;
}
